import { IMonster } from "../../models/monsters.model";
import {
  MonsterDetailActions,
  GET_MONSTER_BY_ID_SUCCESS,
  GetMonsterByIdSuccess,
  GET_MONSTER_BY_ID_FAIL,
  GetMonsterByIdFail
} from "../actions/monster-detail.action";

export interface IMonsterDetailState {
  monster: IMonster;
  error: any;
}

export const initialMonsterDetailState = {
  monster: null,
  error: null
};

export function reduceMonsterDetail(
  state = initialMonsterDetailState,
  action: MonsterDetailActions
): IMonsterDetailState {
  switch (action.type) {
    case GET_MONSTER_BY_ID_SUCCESS: {
      const monster = (<GetMonsterByIdSuccess>action).monster;
      return {
        ...state,
        monster: monster
      };
    }
    case GET_MONSTER_BY_ID_FAIL: {
      const error = (<GetMonsterByIdFail>action).error;
      return {
        ...state,
        error: error
      };
    }
  }
  return state;
}

export const monster = (state: IMonsterDetailState) => state.monster;
export const error = (state: IMonsterDetailState) => state.error;



