import { Injectable } from "@angular/core";

import { Effect, Actions } from "@ngrx/effects";

import { of } from "rxjs/observable/of";
import { map, switchMap, catchError } from "rxjs/operators";

import { MonsterService } from "../../services/monster.service";
import {
  GET_MONSTER_BY_ID,
  GetMonsterById,
  GetMonsterByIdSuccess,
  GetMonsterByIdFail
} from "../actions/monster-detail.action";

@Injectable()
export class MonsterDetailEffects {
  @Effect()
  monster = this.actions
    .ofType(GET_MONSTER_BY_ID)
    .pipe(
      map((action: GetMonsterById) => action.id),
      switchMap(id =>
        this.monsterService
          .byId(id)
          .pipe(
            map(monster => new GetMonsterByIdSuccess(monster)),
            catchError(error => of(new GetMonsterByIdFail(error)))
          )
      )
    );

  constructor(
    private actions: Actions,
    private monsterService: MonsterService
  ) {}
}



