import { Component, OnInit, Input } from "@angular/core";
import { ActivatedRoute, Router, ParamMap } from "@angular/router";

import { Observable } from "rxjs/Observable";
import "rxjs/add/operator/switchMap";

import { Store } from "@ngrx/store";

import * as fromModels from "../../models";
import * as fromStore from "../../store";
import { GetMonsterById } from "../../store";

@Component({
  selector: "app-monster-detail",
  templateUrl: "./monster-detail.component.html",
  styleUrls: ["./monster-detail.component.css"]
})
export class MonsterDetailComponent implements OnInit {
  monster: Observable<fromModels.IMonster>;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private store: Store<fromStore.IAppState>
  ) {}

  ngOnInit() {
    this.monster = this.store.select(fromStore.monsterDetailMonster);
    this.route.paramMap.subscribe((params: ParamMap) => {
      this.store.dispatch(new GetMonsterById(parseInt(params.get("id"))));
    });
  }
}



