import { Action } from "@ngrx/store";
import { IMonster } from "../../models/monsters.model";

export const GET_MONSTER_BY_ID = "[Monster] Get by Id";
export const GET_MONSTER_BY_ID_SUCCESS = "[Monster] Get by Id Success";
export const GET_MONSTER_BY_ID_FAIL = "[Monster] Get by Id Fail";

export class GetMonsterById implements Action {
  readonly type = GET_MONSTER_BY_ID;
  constructor(public id: number) {}
}

export class GetMonsterByIdSuccess implements Action {
  readonly type = GET_MONSTER_BY_ID_SUCCESS;
  constructor(public monster: IMonster) {}
}

export class GetMonsterByIdFail implements Action {
  readonly type = GET_MONSTER_BY_ID_FAIL;
  constructor(public error: any) {}
}

export type MonsterDetailActions =
  | GetMonsterById
  | GetMonsterByIdSuccess
  | GetMonsterByIdFail;



